
.main_body {
  /* background: url(https://img.vrviu.com/images/about_bg.png) bottom no-repeat;
  background-size: 120% 5.18rem; */
  margin-top: 0.5rem;
}
























/* -----左侧导航栏------*/
@-moz-document url-prefix() {
}
.plus {
  float: right;
  font-size: 0.18rem;
  margin-right: 0.05rem;
  transform: scale(1.35, 1) rotate(180deg);
  -ms-transform: scale(1.35, 1) rotate(180deg);
  -moz-transform: scale(1.35, 1) rotate(180deg);
  -webkit-transform: scale(1.35, 1) rotate(180deg);
  -o-transform: scale(1.35, 1) rotate(180deg);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.sub {
  float: right;
  font-size: 0.18rem;
  margin-top: 0.04rem;
  margin-right: 0.05rem;
  transform: scale(1.35, 1);
  -ms-transform: scale(1.35, 1);
  -webkit-transform: scale(1.35, 1);
  -o-transform: scale(1.35, 1);
  -moz-transform: scale(1.35, 1);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.left_nav {
  width: 17%;
}
.left_nav::-webkit-scrollbar {
  width: .07rem;
  /* background: #f9f9f9; */
}
.left_nav::-webkit-scrollbar-track-piece {
  background-color: #fafafa;
}
.left_nav::-webkit-scrollbar-thumb {
  background-color: #d1d1d1;
  border-radius: .1rem;
}
.left_nav ul {
  margin: 0;
  width: 100%;
}
.left_nav>div:first-of-type {
  margin-top: 0.25rem;
}
.left_nav>div:last-of-type {
  margin-bottom: 0.25rem;
}
.left_nav li {
  margin: 0;
  height: 0.6rem;
}
.left_nav a {
  color: #404040;
  display: block;
  height: 0.6rem;
  font-size: 0.18rem;
  text-align: left;
  line-height: 0.6rem;
  padding-left: 0.4rem;
  padding-right: 0.2rem;
  overflow: hidden;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  -webkit-transition: background .1s ease-in-out 0s;
  transition: background .1s ease-in-out 0s;
}
.left_nav .level_plus {
  color: #404040;
  display: block;
  height: 0.6rem;
  font-size: 0.18rem;
  text-align: left;
  line-height: 0.6rem;
  /* padding-left: 0.4rem; */
  padding-right: 0.2rem;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  -webkit-transition: background .1s ease-in-out 0s;
  transition: background .1s ease-in-out 0s;
}
.left_nav a:visited {
  color: #404040;
}
.left_nav .level_ul {
  color: #404040;
}
.left_nav a:hover {
  background: #efefef;
}
.left_nav .level_plus:hover {
  background: #efefef;
}
.left_nav .li_active a {
  color: #369fe8;
  border-right: .04rem solid #368fe8;
}
.left_nav .level_ul {
  height: 0;
  opacity: 0;
  overflow: auto;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  /* transition: height .18s ease-in-out 0s, opacity .18s ease-in-out 0s;  */
}
.left_nav .open {
  opacity: 1;
  height: 100%;
  /* transform: scale(1,1); */
}


































/*---------------------*/
.oTop {
  margin: 0;
  width: 0.5rem;
  height: 0.5rem;
  /* opacity: .85; */
  background: #e9e9e9;
  position: fixed;
  top: 80vh;
  left: 18rem;
  cursor: pointer;
  color: #aaa;
  border-radius: 3px;
  -webkit-transition: all .1s ease-in-out;
  transition: all .1s ease-in-out;
}
.oTop:hover {
  background: #5cadff;
}
.oTop div {
  margin: .038rem auto;
  font-size: 0.5rem;
  transform: scale(1.5, 1);
  -ms-transform: scale(1.5, 1);
  -webkit-transform: scale(1.5, 1);
  -o-transform: scale(1.5, 1);
  -moz-transform: scale(1.5, 1);
}
.oTop div:hover {
  color: white;
}
































/* -----右侧显示栏------*/
.right_rev {
  width: 83%;
  border-left: 0.01rem solid #e9e9e9;
}
.right_rev .search {
  width: 100%;
  height: 0.5rem;
}
.right_rev #ser {
  /* margin-bottom: 0.2rem; */
  /* margin-left: 12.5rem;  */
  padding-top: .1rem;
  margin-top: .3rem;
  width: 0.4rem;
  cursor: pointer;
  transition: margin .2s ease-in-out 0s;
  -webkit-transition: margin .2s ease-in-out 0s;
  -o-transition: margin .2s ease-in-out 0s;
  -moz-transition: margin .2s ease-in-out 0s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  opacity: .5;
  z-index: 999;
}
.right_rev #ser:hover {
  opacity: 1;
}
.ser_png {
  margin-left: 12.5rem;
}
.ser_png_active {
  margin-left: 12.2rem;
}
.right_rev #search_input {
  height: .3rem;
  width: 3rem;
  font-size: .18rem;
  padding: .1rem;
  color: #333F5C;
  border: 0.01rem solid rgba(0, 0, 0, .1);
  transition: margin .2s ease-in-out 0s, opacity .2s ease-out 0s, border .2s ease-in-out 0s;
  -webkit-transition: margin .2s ease-in-out 0s, opacity .2s ease-out 0s, border .2s ease-in-out 0s;
  -o-transition: margin .2s ease-in-out 0s, opacity .2s ease-out 0s, border .2s ease-in-out 0s;
  -moz-transition: margin .2s ease-in-out 0s, opacity .2s ease-out 0s, border .2s ease-in-out 0s;
  margin-top: .3rem;
  /* border: 0;  */
  float: right;
}
.right_rev #search_input:hover {
  border: .01rem #5cadff solid;
}
.right_rev #search_input:focus {
  border: .01rem #5cadff solid;
}
.ser_input {
  margin-right: .3rem;
  opacity: 0;
  visibility: hidden;
}
.ser_input_active {
  margin-right: .6rem;
  opacity: .8
}


































/*--------------------*/
.english32 {
  line-height: 2em!important;
}
.english {
  width: 70%;
  margin-left: 1.8rem;
  /*  text-align: justify!important;
  text-justify: inter-ideograph!important;*/
}
.english2 {
  margin-top: 0.3rem;
}
.content {
  width: 16rem;
  margin: 0 auto;
  text-align: center;
  /* border: 0.01rem solid #f5f5f5; */
  background: rgba(255, 255, 255, 1);
  /* background: white; */
  margin-top: 0.46rem;
  padding-bottom: 0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.title {
  color: #333F5C;
  font-size: 0.4rem;
  padding: 0.8rem 0 0.5rem 0;
}
.text1 {
  font-size: 0.20rem;
  color: #333F5C;
  line-height: 0.36rem;
  text-align: left;
}
.pic_list .title1 {
  font-size: 0.32rem;
  color: #333F5C;
}
.pic_list .title2 {
  font-size: 0.22rem;
  color: #333F5C;
}
.pic_list .title3 {
  font-size: 0.16rem;
  color: #333F5C;
  line-height: 1.6em;
  width: 90%;
  margin: 0 auto;
  text-align: center;

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.pic_list li {
  float: left;
  margin-right: 0.27rem;
  width: 3.83rem;
  height: 5.73rem;
  background: #fff;
  margin-bottom: 0.49rem;
}
.pic_list {
  margin-right: -0.3rem;
  overflow: hidden;
  margin-bottom: 0.3rem;
}
.pic_list li img {
  height: 3.83rem;
  width: 3.83rem;
}
.footer {
  position: static;
}
.content_mask {
  margin: 0 2rem;
}



