
#content code {
    /* color: #000; */
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    direction: ltr;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}
#content {
    margin: .5rem 1rem 0 1rem;
    font-size: 0.20rem;
    color: #333F5C;
    line-height: 0.36rem;
    text-align: left;
    /* border-top: .01rem #e9e9e9 solid;
    padding-top: .25rem; */
}
#content img {
    max-width: 100%;
}
h1,
h2 {
    padding-bottom: .25rem;
    border-bottom: .01rem #e9e9e9 solid;
}
#content ul {
    list-style-type: disc;
    margin-left: .2rem;
}
#content a:link,
#content a:visited {
    color: #369fe8;
}
#content a:hover {
    text-decoration: underline;
}
#content table {
    border-top: .01rem #e9e9e9 solid;
    border-buttom: .01rem #e9e9e9 solid;
    border-collapse: collapse;
}
#content th,
#content td {
    margin: 0;
    padding: .2rem 0 .2rem .2rem;
    border-bottom: 0.01rem solid #e9e9e9;
    width: 20%;
    text-overflow: ellipsis;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#content th {
    background: #f2f2f2;
}
#content tr {
    transition: .1s background ease-in-out 0s, .2s box-shadow ease-in-out 0s;
    -webkit-transition: .1s background ease-in-out 0s, .2s box-shadow ease-in-out 0s;
    -moz-transition: .1s background ease-in-out 0s, .2s box-shadow ease-in-out 0s;
}
#content tr:hover {
    background: #f9f9f9;
    -webkit-box-shadow: 0px 2px 11px -2px rgba(0, 0, 0, 0.39);
    box-shadow: 0px 2px 11px -2px rgba(0, 0, 0, 0.39);
}
#content blockquote {
    background: #f9f9f9;
    width: 100%;
    margin: 0;
    /* padding: .2rem 0; */
}
#content blockquote p {
    padding: .2rem .5rem;
    line-height: .45rem;
    /* border-left: .05rem solid #ff5e29; */
}
#content table {
    width: 100%;
}
#content pre {
    white-space: wrap;
    line-height: .3rem;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    counter-reset: linenumber;
    display: block;
    overflow-x: auto;
    padding: .2rem .3rem;
    color: #1b2127;
    background: #f5f7f8;
    text-shadow: none;
    border-left: .05rem solid #369fe8;
}



