
.icon_ch,
.icon_en {
    background: url(https://img.vrviu.com/images/home_nva_img_English.png) no-repeat;
    background-size: cover;
    display: inline-block;
    width: 0.18rem;
    height: 0.18rem;
    vertical-align: middle;
    margin-right: 0.1rem;
}
.icon_en {
    background: url(https://img.vrviu.com/images/home_nva_img_Chinese.png) no-repeat;
    background-size: cover;
}
.page-header img {
    position: absolute;
    left: 0.3rem;
    top: 0.3rem;
    width: 1.56rem;
    height: 0.34rem;
}
.page-header {
    background: #fff;
    line-height: 1;
    width: 100%;
    position: relative;
    z-index: 9;
}
.Header {
    overflow: hidden;
    margin: 0 auto;
    position: relative;
    z-index: 10;
    vertical-align: middle;
    width: 80%;
    background: #fff;
    font-size: 0.14rem;
    height: 1rem;
}
.Header_right {
    font-size: 0.14rem;
    z-index: 100;
    float: right;
    margin-top: 0.4rem
}
.Header_right a {
    color: #404040;
    font-size: 0.18rem!important;
    -webkit-text-decoration-line: none;
            text-decoration-line: none
}
.Header_right a:hover {
    color: #369fe8;
}
.Header_right .active {
    color: #369fe8!important;
}
.active {
    background: url(https://img.vrviu.com/images/NVA_ICON.png) center 0.3rem no-repeat;
}
.Header_right li {
    height: 0.5rem;
    float: left;
    color: #404040;
    margin-right: 0.5rem;
}



